import styled from 'styled-components'

import {
    theme
} from '../constants'

import {
    above,
    under
} from '../helpers/breakpoints'

export const StyledHero = styled.div`
    width: 100%;
    height: 100%;

    color: ${theme.colorDefault};

    overflow: hidden;

    @media ${above.tablet} {
        padding-top: ${theme.s(4)};
        padding-bottom: ${theme.s(4)};
    }
`

export const StyledHeroContent = styled.div`
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;

    z-index: 10;
`

export const StyledHeroContentWrapper = styled.div`
    width: 100%;

    @media ${above.tablet} {
        margin-top: ${theme.s(9)};
    }
    @media ${above.mobile} and ${under.smDesktop} {
        max-width: 90%;
    }
    @media ${above.smDesktop} {
        max-width: 60%;

        margin-top: ${theme.s(20)};
    }
`

export const StyledHeading = styled.h1`
    font-size: 1.6em;
    font-weight: 400;

    line-height: 1.4;

    margin: 0;
    margin-bottom: ${theme.s(2)};

    color: ${theme.colorPrimary};

    @media ${above.tablet} {
        font-size: 2.1em;
        margin-bottom: ${theme.s(4)};
    }
    @media ${above.smDesktop} {
        font-size: 2.8em;
    }
`

export const StyledDesc = styled.div`
    font-size: 1rem;
    line-height: 1.8;

    margin: 0;
    margin-bottom: ${theme.s(2)};

    max-width: 100%;

    p {
        margin-bottom: .7rem;
    }

    @media ${above.tablet} {
        margin-bottom: ${theme.s(4)};
    }
    @media ${above.desktop} {
        max-width: 90%;

        font-size: .85rem;
    }
`

export const StyledBgImage = styled.img.attrs(props => ({
    style: {
        top: props.top,
        left: props.left,
        bottom: props.bottom,
        right: props.right
    }
}))`
    position: absolute;

    ${props => props.mw ? `max-width: ${props.mw}; height: auto;` : ''}

    z-index: 9;

    @media ${under.smDesktop} {
        display: none;
    }
    @media ${above.desktop} {
        ${props => props.mwDesktop ? `max-width: ${props.mwDesktop}; height: auto;` : ''}
    }
`

export const StyledBgColor = styled.div.attrs(props => ({
    style: {
        top: props.top,
        left: props.left,
        bottom: props.bottom,
        right: props.right
    }
}))`
    display: block;

    position: absolute;

    ${props => props.mw ? `max-width: ${props.mw}; height: auto;` : ''}

    background-color: ${props => props.bg ? props.bg : 'rgba(255, 255, 255, .7)'};

    width: ${props => props.dimensions};
    height: ${props => props.dimensions};

    ${props => props.rounded ? `border-radius: 100%;` : ''}

    @media ${under.smDesktop} {
        display: none;
    }
`

export const StyledStrong = styled.strong`
    position: relative;

    ::before {
        content: '';

        position: absolute;
        bottom: 5px;
        left: 0;

        width: 100%;
        height: 30%;

        z-index: -1;

        background-color: ${theme.colorOrange};

        animation-duration: 1.5s;
        animation-name: titleBg;
        animation-timing-function: ease-in-out;
    }
`

export const StyledUl = styled.ul`
    line-height: 1.3;
    margin-bottom: ${theme.s(2)};

    li {
        margin-bottom: ${theme.s()};
    }

    @media ${above.tablet} {
        margin-bottom: ${theme.s(3)};

        li {
            margin-bottom: ${theme.s(1.5)};
        }
    }
`

export const StyledCountdown = styled.div`
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;

    margin: ${theme.s(2)} 0;
    margin-bottom: ${theme.s(2)};

    text-align: center;

    p {
        margin: 0;

        padding: ${theme.s(2)};

        text-align: left;

        flex: 0 1 50%;

        &:first-of-type {
            background-color: ${theme.colorOrange};

            span {
                color: white;
            }
        }
        &:last-of-type {
            background-color: white;
        }
    }

    @media ${above.tablet} {
        margin-bottom: ${theme.s(4)};

        max-width: 60%;
    }
    @media ${above.desktop} {
        margin: 0;
        margin-bottom: ${theme.s(4)};

        font-size: 1rem;
    }
`
